import axios from 'axios'

const url = '/api'

class LectureService {
    static async CreateLecture(lectureBody) {
        const response = await axios.post(url + '/lectures', lectureBody)
        return response.data
    }

    static async GetLecture(lectureId) {
        const response = await axios.get(url + '/lectures/' + lectureId)
        return response.data
    }

    static async GetHomeworks(lectureId) {
        const response = await axios.get(url + '/lectures/' + lectureId + '/homeworks')
        return response.data
    }

    static async DeleteLecture(lectureId) {
        const response = await axios.delete(url + '/lectures/' + lectureId)
        return response.data
    }

    static async GetStudents(lectureId) {
        const response = await axios.get(url + '/lectures/' + lectureId + '/students')
        return response.data
    }

    static async UnenrollStudents(lectureId, students) {        
        const response = await axios.delete(url + '/lectures/' + lectureId + '/unenroll', students)
        return response.data
    }
}

export default LectureService;