<template>
  <div>
    <v-row>
      <v-col cols="2">
        <v-textarea rows="1" label="Student Name" v-model="inputName" outlined>
        </v-textarea>
      </v-col>
      <v-col cols="2">
        <v-btn
          color="accent"
          @click="createStudent"
          :disabled="inputName.length < 3"
        >
          New Student
        </v-btn>
      </v-col>
      <v-col cols="2">
        <v-btn
          color="accent"
          :disabled="selectedStudents.length < 1"
          @click="openEnrollDialog()"
        >
          Enroll Student
        </v-btn>
      </v-col>
    </v-row>
    <v-card dark>
      <v-card-title>
        <v-spacer></v-spacer>
        <v-text-field
          v-model="studentSearch"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        ></v-text-field>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="students"
        :search="studentSearch"
        show-select
        @click:row="goToStudentProfile"
        v-model="selectedStudents"
        class="elevation-1"
        dark
      >
        <template v-slot:item.action="{ item }">
          <v-icon @click="deleteItem(item)" color="error">
            mdi-delete
          </v-icon>
        </template>
      </v-data-table>
    </v-card>

    <v-dialog v-model="dialog" scrollable max-width="60vw">
      <v-card dark>
        <v-card-title>
          <v-spacer></v-spacer>
          <v-text-field
            v-model="lectureSearch"
            append-icon="mdi-magnify"
            label="Search"
            single-line
            hide-details
          ></v-text-field>
        </v-card-title>
        <v-data-table
          :headers="lecturesHeaders"
          :items="lectures"
          :search="lectureSearch"
          show-select
          v-model="selectedLectures"
          class="elevation-1"
        >
          <template v-slot:item.action="{ item }">
            <v-icon @click="deleteItem(item)" color="error">
              mdi-delete
            </v-icon>
          </template>
        </v-data-table>
        <v-card-actions>
          <v-btn color="success" @click="enrollStudent">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import SchoolService from "../services/SchoolService";
import StudentService from "../services/StudentService";
import CourseService from "../services/CourseService";
import TeacherService from "../services/TeacherService";

export default {
  data() {
    return {
      students: [],
      selectedStudents: [],
      selectedLectures: [],
      lectures: [],
      teachers: [],
      courses: [],
      dialog: false,
      headers: [
        {
          text: "Name",
          align: "start",
          value: "name"
        },
        { text: "Actions", value: "action", sortable: false }
      ],
      lecturesHeaders: [
        {
          text: "Course",
          align: "start",
          value: "course"
        },
        {
          text: "Teacher",
          value: "teacher"
        }
      ],
      inputName: "",
      lectureSearch: "",
      studentSearch: ""
    };
  },
  created() {
    SchoolService.GetStudents(1).then(students => {
      this.students = students;
    });
  },
  methods: {
    deleteItem(item) {
      const index = this.students.indexOf(item);
      const isConfirmed = confirm("Are you sure you want to delete this item?");
      if (isConfirmed) {
        StudentService.DeleteStudent(item.id).then(() => {
          this.students.splice(index, 1);
        });
      }
    },

    createStudent() {
      StudentService.CreateStudent({
        name: this.inputName
      }).then(() => {
        this.$router.go();
      });
    },

    openEnrollDialog() {
      this.dialog = true;

      if (this.lectures.length == 0) {
        SchoolService.GetLectures(1).then(lectures => {
          this.lectures = lectures;

          for (let i = 0; i < lectures.length; i++) {
            const el = lectures[i];
            TeacherService.GetTeacherById(el.teacherId).then(teacher => {
              el.teacher = teacher.name;
              this.lectures.splice(i, 1, el);
            });
            CourseService.GetCourseById(el.courseId).then(course => {
              el.course = course.name;
              this.lectures.splice(i, 1, el);
            });
          }
        });
      }
    },

    enrollStudent() {
      const studentIds = this.selectedStudents.map(x => x.id);
      const lectureIds = this.selectedLectures.map(x => x.id);
      StudentService.EnrollStudent(studentIds, lectureIds).then(() => {
        this.$router.go();
      });
    },

    goToStudentProfile(clickedRow) {
      this.$router.push({
        name: "ViewStudent",
        params: { studentId: clickedRow.id }
      });
    }
  }
};
</script>

