<template>
  <div>
    <h1>{{ homework.name }}</h1>
    <h1>
      {{ student.name }} - {{ assignment.updatedAt }} - {{ assignment.mark }}
    </h1>
    <v-divider></v-divider>
    <v-row>
      <v-col
        cols="6"
        v-for="(question, index) in homework.questions"
        :key="index"
      >
        <v-card raised>
          <v-card-title>
            <v-row>
              <v-col cols="10" class="text-left display-1 font-weight-black">{{ question.text }}</v-col>
              <v-col cols="2">
                <v-textarea rows="1" no-resize outlined> </v-textarea>
              </v-col>
            </v-row>
          </v-card-title>

          <v-card-text class="text-left font-weight-medium title black--text">
            {{ assignment.answers[index] }}
          </v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import StudentService from "../services/StudentService";
export default {
  data() {
    return {
      assignmentId: Number,
      assignment: {},
      homework: {},
      student: {}
    };
  },
  created() {
    this.assignmentId = this.$route.params.assignmentId;
    this.assignment = this.$route.params.assignment;
    this.homework = this.$route.params.homework;
    StudentService.GetStudent(this.assignment.studentId).then(student => {
      this.student = student;
    });
  }
};
</script>

