<template>
  <div>
    <v-row>
      <v-col cols="2">
        <v-textarea rows="1" label="Teacher Name" v-model="inputName" outlined>
        </v-textarea>
      </v-col>
      <v-col cols="2">
        <v-btn color="accent" @click="create">New Teacher</v-btn>
      </v-col>
    </v-row>
    <v-data-table :headers="headers" :items="teachers" class="elevation-1" dark>
      <template v-slot:item.action="{ item }">
        <v-icon @click="deleteItem(item)" color="error">
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import SchoolService from "../services/SchoolService";
import TeacherService from "../services/TeacherService";
export default {
  data: () => ({
    dialog: false,
    headers: [
      {
        text: "Name",
        align: "start",
        value: "name"
      },
      { text: "Group 1", value: "group1" },
      { text: "Group 2", value: "group2" },
      { text: "Group 3", value: "group3" },
      { text: "Actions", value: "action", sortable: false }
    ],
    teachers: [],
    inputName: ""
  }),
  created() {
    SchoolService.GetTeachers(1).then(teachers => {
      this.teachers = teachers;
    });
  },
  methods: {
    deleteItem(item) {
      const index = this.teachers.indexOf(item);
      confirm("Are you sure you want to delete this item?") &&
        this.teachers.splice(index, 1);
    },
    create() {
      TeacherService.CreateTeacher({
        name: this.inputName
      }).then(() => {
        this.$router.go()
      });
    }
  }
};
</script>