import axios from 'axios'

const url = '/api'

class TeacherService {
    static async CreateTeacher(teacherBody) {
        const response = await axios.post(url + '/teachers', teacherBody)
        return response.data
    }

    static async GetTeacherById(teacherId) {
        const response = await axios.get(url + '/teachers/' + teacherId)
        return response.data
    }

    static async DeleteTeacher(teacherId) {
        const response = await axios.delete(url + '/teachers/' + teacherId)
        return response.data
    }
}

export default TeacherService;