import axios from 'axios'

const url = '/api'

class HomeworkService {
    static async GiveHomework(homeworkBody) {
        const response = await axios.post(url + '/homeworks/', homeworkBody)
        return response.data
    }

    static async GetAssignments(homeworkId) {
        const response = await axios.get(url + '/homeworks/' + homeworkId + '/assignments')
        return response.data
    }

    static async GetHomework(homeworkId) {
        const response = await axios.get(url + '/homeworks/' + homeworkId)
        return response.data
    }

    static async DeleteHomework(homeworkId) {
        const response = await axios.delete(url + '/homeworks/' + homeworkId)
        return response.data
    }
}

export default HomeworkService;