<template>
  <div>
    <v-row>
      <v-col cols="2">
        <v-btn color="accent" large @click.stop="showHomeworkForm = true">
          Yeni Ödev
        </v-btn>
      </v-col>

      <HomeworkForm v-model="showHomeworkForm" :lectureId="lectureId" />

      <h1>{{ course.name }}</h1>
    </v-row>

    <v-divider></v-divider>

    <v-card dark>
      <v-card-title>
        <v-btn
          icon
          color="error"
          @click="unenrollStudent()"
          :disabled="selectedStudents.length < 1"
        >
          <v-icon>mdi-delete</v-icon>
        </v-btn>
        <v-spacer></v-spacer>
        <v-text-field
          v-model="studentSearch"
          append-icon="mdi-magnify"
          label="Search"
          single-line
          hide-details
        >
        </v-text-field>
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="students"
        :search="studentSearch"
        show-select
        v-model="selectedStudents"
      >
        <template v-slot:item.action="{ item }">
          <v-icon @click="viewStudentPerformance(item)">
            mdi-account-search
          </v-icon>
        </template>
      </v-data-table>
    </v-card>

    <v-row>
      <v-col
        cols="12"
        sm="6"
        md="4"
        v-for="(homework, index) in homeworks"
        :key="index"
        @click="goToHomework(homework)"
      >
        <v-card dark color="primary" shaped>
          <v-card-title>
            <v-icon class="mr-2">mdi-book</v-icon>
            {{ homework.name }}
            <v-btn icon @click="deleteHomework($event, homework.id)">
              <v-icon>mdi-delete</v-icon>
            </v-btn>
          </v-card-title>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import LectureService from "../services/LectureService";
import CourseService from "../services/CourseService";

import HomeworkForm from "../components/HomeworkForm";
import HomeworkService from "../services/HomeworkService";

export default {
  components: {
    HomeworkForm
  },
  data() {
    return {
      lecture: {},
      course: {},
      homeworks: [],
      students: [],
      selectedStudents: [],
      studentSearch: "",
      lectureId: Number,
      showHomeworkForm: false,
      headers: [
        {
          text: "Student",
          value: "name"
        },
        { text: "Average", value: "average" },
        { text: "Actions", value: "action", sortable: false }
      ]
    };
  },
  created() {
    this.lectureId = this.$route.params.lectureId;
    LectureService.GetStudents(this.lectureId).then(students => {
      this.students = students;
    });

    LectureService.GetLecture(this.lectureId).then(lecture => {
      this.lecture = lecture;
      LectureService.GetHomeworks(this.lectureId).then(homeworks => {
        this.homeworks = homeworks;
      });
      CourseService.GetCourseById(lecture.courseId).then(course => {
        this.course = course;
      });
    });
  },
  methods: {
    goToHomework(homework) {
      this.$router.push({
        name: "Assignments",
        params: { homeworkId: homework.id, homework: homework }
      });
    },
    deleteHomework(event, homeworkId) {
      event.preventDefault();
      HomeworkService.DeleteHomework(homeworkId);
    },
    unenrollStudent(lectureId) {
      let studentIds = [];
      for (let i = 0; i < this.selectedStudents.length; i++) {
        const el = this.selectedStudents[i];
        studentIds.push(el.id);
      }
      console.log(studentIds);

      LectureService.UnenrollStudents(lectureId, studentIds).then(() => {
        // this.$router.go()
      });
    },
    viewStudentPerformance(student) {
      console.log("View student performance");
      console.log(student);
    }
  }
};
</script>

