<template>
  <div>
    <v-row>
      <v-col cols="2">
        <v-select
          label="Course"
          outlined
          item-text="name"
          item-value="id"
          v-model="selectedCourse"
          :items="courses"
        ></v-select>
      </v-col>
      <v-col cols="2">
        <v-select
          label="Teacher"
          outlined
          item-text="name"
          item-value="id"
          v-model="selectedTeacher"
          :items="teachers"
        ></v-select>
      </v-col>
      <v-col cols="2">
        <v-select
          label="Semester"
          outlined
          v-model="selectedSemester"
          :items="semesters"
        ></v-select>
      </v-col>
      <v-col cols="2">
        <v-btn color="accent" @click="create">New Lecture</v-btn>
      </v-col>
    </v-row>

    <v-data-table
      :headers="headers"
      :items="lectures"
      class="elevation-1"
      @click:row="goToLectureProfile"
      dark
    >
      <template v-slot:item.action="{ item }">
        <v-icon @click="deleteItem(item)" color="error">
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
  </div>
</template>

<script>
import SchoolService from "../services/SchoolService";
import LectureService from "../services/LectureService";
import TeacherService from "../services/TeacherService";
import CourseService from "../services/CourseService";

export default {
  data() {
    return {
      lectures: [],
      teachers: [],
      courses: [],
      semesters: ["Winter", "Spring", "Summer", "Fall"],
      selectedSemester: "",
      selectedTeacher: "",
      selectedCourse: "",
      headers: [
        {
          text: "Course",
          value: "course"
        },
        { text: "Teacher", value: "teacher" },
        { text: "Semester", value: "semester" },
        { text: "Actions", value: "action", sortable: false }
      ],
      inputName: ""
    };
  },
  created() {
    SchoolService.GetLectures(1).then(lectures => {
      this.lectures = lectures;
      for (let i = 0; i < lectures.length; i++) {
        const el = lectures[i];

        TeacherService.GetTeacherById(el.teacherId).then(teacher => {
          el.teacher = teacher.name;
          this.lectures.splice(i, 1, el);
        });
        CourseService.GetCourseById(el.courseId).then(course => {
          el.course = course.name;
          this.lectures.splice(i, 1, el);
        });
      }
    });
    SchoolService.GetCourses(1).then(courses => {
      this.courses = courses;
    });
    SchoolService.GetTeachers(1).then(teachers => {
      this.teachers = teachers;
    });
  },
  methods: {
    deleteItem(item) {
      const index = this.lectures.indexOf(item);
      const isConfirmed = confirm("Are you sure you want to delete this item?");
      if (isConfirmed) {
        LectureService.DeleteLecture(item.id).then(() => {
          this.lectures.splice(index, 1);
        });
      }
    },
    create() {
      LectureService.CreateLecture({
        courseId: this.selectedCourse,
        teacherId: this.selectedTeacher,
        semester: this.selectedSemester
      }).then(() => {
        this.$router.go();
      });
    },
    goToLectureProfile(clickedRow) {
      this.$router.push({
        name: "ViewLecture",
        params: { lectureId: clickedRow.id.toString() }
      });
    }
  }
};
</script>

